import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
const Another = (props) => {
    return (
        <div>
            <img src="https://gtms03.alicdn.com/tps/i3/TB1rVYNKXXXXXaKXVXXvBLt6FXX-230-230.png_.webp"/>
            <button onClick={ ()=>props.history.push('/Home') }>返回</button>
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0,

            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"

                    // tabBarPosition="buttom"
                    // hidden={this.state.hidden}
                    // prerenderingSiblingsNumber={0}
                    
                >
                    <TabBar.Item
                        title="店铺"
                        key="home"
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-yemian-copy-copy'></i>                           
                        }
                        // selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="客服"
                        key="cart"
                        icon={
                            <i className='iconfont icon-kefu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche1'></i>                           
                        }
                        // selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        title="收藏"
                        key="Friend"
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-liebiao1'></i>  
                        }
                        // selected={ pathname === '/dingdan' }
                        onPress={ () => {
                            this.props.history.push('/dingdan')
                        } }
                        data-seed="logId2"
                    >
                        
                    </TabBar.Item>
                            <TabBar.Item
                                title="加入购物车"
                                key="my"
                                icon={<i className=''></i> }
                                selectedIcon={ 
                                    <i className='iconfont icon-041'></i>  
                                }
                                
                                // selected={ pathname === '/mytaobao' }
                                onPress={ () => {
                                    this.props.history.push('/mytaobao')
                                } }
                                data-seed="logId3"
                            >
                                
                    </TabBar.Item>
                    <TabBar.Item
                                title="立即购买"
                                key="my"
                                icon={<i className=''></i> }
                                selectedIcon={ 
                                    <i className='iconfont icon-041'></i>  
                                }
                                
                                // selected={ pathname === '/mytaobao' }
                                onPress={ () => {
                                    this.props.history.push('/mytaobao')
                                } }
                                data-seed="logId3"
                            >
                                
                    </TabBar.Item>
                </TabBar>
            </div>
        </div>
    )
}

export default Another
 